import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Grid, Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper} from '@material-ui/core/';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    header: {
        marginTop: 100,
        minHeight: 150,
        backgroundColor: theme.palette.primary.main,
        textAlign: 'center',
        paddingTop: 40,
    },
    headerText: {
        color: theme.palette.primary.dark,
    },
    table: {
        paddingTop: 50,
    },
}));


function createData(name, time, price) {
    return { name, time, price };
}

const rows = [
    createData('Usługa 1', '60 min', 120),
    createData('Usługa 2', '60 min', 60),
    createData('Usługa 3', '120 min', 450),
    createData('Usługa 4', '45 min', 120),
    createData('Usługa 5', '30 min', 150),
    createData('Usługa 6', '90 min', 100),
    createData('Usługa 7', '50 min', 100),
    createData('Usługa 8', '30 min', 100),
];


export default function Cennik() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <Grid container direction="column">
                <Grid item justifyContent="center" alignItems="center" className={classes.header}>
                    <Typography variant="h2" className={classes.headerText}>
                        Cennik
                    </Typography>
                </Grid>
                <Grid item className={classes.table}>
                    <Container maxWidth="md">
                        <TableContainer component={Paper}>
                            <Table className={classes.table} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>Nazwa zabiegu</TableCell>
                                        <TableCell align="right">Czas trwania</TableCell>
                                        <TableCell align="right">Cena</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {rows.map((row) => (
                                        <TableRow key={row.name}>
                                            <TableCell component="th" scope="row">
                                                {row.name}
                                            </TableCell>
                                            <TableCell align="right">{row.time}</TableCell>
                                            <TableCell align="right">{row.price} zł</TableCell>
                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Container>
                </Grid>
            </Grid>
        </div>
    )
}
