import React from 'react';
import { Link } from "react-router-dom";
import { AppBar, Toolbar, Typography, Button, Menu, MenuItem} from '@material-ui/core';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import { makeStyles } from '@material-ui/core/styles';
import logo from './images/logo.png';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    appbar: {
        color: '#000000',
        backgroundColor: '#ffffff',
    },
    logo: {
        height: 100,
    },
    title: {
        flexGrow: 1,
    },
    button: {
        marginLeft: 24,
        marginRight: 24,
        '&:hover': {
            backgroundColor: '#ffffff',
            color: '#005194', //'#003c6e',   // Light blue - dark
        }
    },
    dropdownMenu: {
        minWidth: 200,
    }
}));

export default function AppNav() {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    const handleClick = (event) => {
      setAnchorEl(event.currentTarget);
    };
    const handleClose = () => {
      setAnchorEl(null);
    };

    return (
        <div className={classes.root}>
            <AppBar position="fixed" className={classes.appbar}>
                <Toolbar>
                    <Typography variant="h6" className={classes.title}>
                        <img alt="Vyspa" src={logo} className={classes.logo} />
                    </Typography>
                    <Button color="inherit" component={Link} to="/" className={classes.button} disableRipple disableFocusRipple>Strona główna</Button>
                    <Button color="inherit" component={Link} to="/o-mnie" className={classes.button} disableRipple disableFocusRipple>O mnie</Button>
                    {/* <Button color="inherit" component={Link} to="/uslugi" className={classes.button} disableRipple disableFocusRipple>Usługi</Button> */}
                    <Button
                        id="basic-button"
                        aria-controls="basic-menu"
                        aria-haspopup="true"
                        aria-expanded={open ? 'true' : undefined}
                        onClick={handleClick}
                        endIcon={<ArrowDropDownIcon  />}
                    >
                        Usługi
                    </Button>
                    <Menu
                        id="basic-menu"
                        anchorEl={anchorEl}
                        open={open}
                        onClose={handleClose}
                        MenuListProps={{
                            'aria-labelledby': 'basic-button',
                        }}
                    >
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/kobido">Kobido</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/kassaji">Kassaji</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/kyukaku">Kyukaku</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/dermoplastyczny">Dermoplastyczny</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/mioplastyczny">Mioplastyczny</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/transbukkalny">Transbukkalny</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/zoga">Zoga Face Integration</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/relaksacyjny">Masaż relaksacyjny</MenuItem>
                        <MenuItem onClick={handleClose} className={classes.dropdownMenu} component={Link} to="/kamienie">Gorące Kamienie</MenuItem>
                    </Menu>
                    <Button color="inherit" component={Link} to="/cennik" className={classes.button} disableRipple disableFocusRipple>Cennik</Button>
                    <Button color="inherit" component={Link} to="/gabinet" className={classes.button} disableRipple disableFocusRipple>Gabinet</Button>
                    <Button color="inherit" component={Link} to="/kontakt" className={classes.button} disableRipple disableFocusRipple>Kontakt</Button>
                </Toolbar>
            </AppBar>
        </div>
    )
}
